/* eslint-disable */

function getChars() {
  const a = 1 << 0;
  const b = 1 << 1;
  const c = 1 << 2;
  const d = 1 << 3;
  const e = 1 << 4;
  const f = 1 << 5;
  const g = 1 << 6;
  const h = 1 << 7;
  const i = 1 << 8;
  const j = 1 << 9;
  const k = 1 << 10;
  const l = 1 << 11;


  const o = {};
  o[0] = ' ';
  o[a] = '&';
  o[b] = '-';
  o[c] = '0';
  o[d] = '1';
  o[e] = '2';
  o[f] = '3';
  o[g] = '4';
  o[h] = '5';
  o[i] = '6';
  o[j] = '7';
  o[k] = '8';
  o[l] = '9';
  //
  o[a|d] = 'A';
  o[a|e] = 'B';
  o[a|f] = 'C';
  o[a|g] = 'D';
  o[a|h] = 'E';
  o[a|i] = 'F';
  o[a|j] = 'G';
  o[a|k] = 'H';
  o[a|l] = 'I';
  //
  o[b|d] = 'J';
  o[b|e] = 'K';
  o[b|f] = 'L';
  o[b|g] = 'M';
  o[b|h] = 'N';
  o[b|i] = 'O';
  o[b|j] = 'P';
  o[b|k] = 'Q';
  o[b|l] = 'R';
  //
  o[c|d] = '/';
  o[c|e] = 'S';
  o[c|f] = 'T';
  o[c|g] = 'U';
  o[c|h] = 'V';
  o[c|i] = 'W';
  o[c|j] = 'X';
  o[c|k] = 'Y';
  o[c|l] = 'Z';
  //
  o[e|k] = ':';
  o[f|k] = '#';
  o[g|k] = '@';
  o[h|k] = '\'';
  o[i|k] = '=';
  o[j|k] = '"';
  //
  o[a|e|k] = '¢';
  o[a|f|k] = '.';
  o[a|g|k] = '<';
  o[a|h|k] = '(';
  o[a|i|k] = '+';
  o[a|j|k] = '|';
  //
  o[b|e|k] = '!';
  o[b|f|k] = '$';
  o[b|g|k] = '*';
  o[b|h|k] = ')';
  o[b|i|k] = ';';
  o[b|j|k] = '^';
  //
  o[c|f|k] = ',';
  o[c|g|k] = '%';
  o[c|h|k] = '_';
  o[c|i|k] = '>';
  o[c|j|k] = '?';

  return o;
}

function getKeyByChar(char) {
  const chars = getChars();
  const keys = Object.keys(chars);
	for(let i = 0; i < keys.length; i += 1) {
		const k = keys[i];
		if(chars[k] === char) {
			return k;
		}
  }

	return 0;
}

export { getChars, getKeyByChar };
