<template>
  <div class="app">
    <div @click="showModal = true" class="help-image-button">
      <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 24 24" fill="#f8f696" stroke="currentColor" stroke-width="3" stroke-linecap="round" stroke-linejoin="round" display="block" id="Info"><circle cx="12" cy="12" r="10"/><path d="M11.5 7h.5"/><path d="M10 11h2v5"/><path d="M10 16h4"/></svg>
    </div>
    <Modal v-if="showModal" @close="showModal = false">
      <h3 slot="header">
        Перфокарты
      </h3>
      <!-- eslint-disable -->
      <div slot="body">
        <p>
          Перфокарты — это носители информации из тонкого картона, данные на которых кодируются с помощью отверстий, проделанных в определенных точках.
        </p>
        <p>
          При работе с перфокартами в текстовом режиме каждая колонка обозначает один символ; таким образом, одна перфокарта представляет строку из 80 символов. Допускаются лишь некоторые комбинации отверстий. Наиболее просто кодируются цифры — одной пробивкой в позиции, обозначенной данной цифрой. Буквы и другие символы кодируются несколькими пробивками в одной колонке. Отсутствие пробивок в колонке означает пробел.
        </p>
        <p>
          Справочник комбинаций отверстий и их расшифровка доступна на <a href="#" target="_blank">странице</a>.
        </p>
      </div>
      <!-- eslint-enable -->
    </Modal>
    <Firework v-if="showFireworks" />
    <div>
      <div class="text-wrapper">
        <h1>Punch card Emulator</h1>
      </div>
      <Emulator
        :str="$store.state.result"
        @handleValueChange="onValueChange"
        class="emulator-wrapper"
      ></Emulator>
      <div class="action-container">
        <div
          v-html="text"
          class="action-text"
        />
        <div class="action-buttons">
          <div v-if="this.result">
            <span>Результат:&nbsp;
              <b
                :style="{ color: isCorrectResult ? 'green' : 'red' }"
                v-html="resultMsg"
              ></b>
            </span>
          </div>
          <input
            v-if="this.result"
            @click="onCheckResultClick"
            class="button green"
            type="button"
            value="Отправить результат"
          >
          <input
            @click="onClearClick"
            class="button gray"
            type="button"
            value="Очистить"
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CryptoJs from 'crypto-js';

import Emulator from './components/Emulator.vue';
import Firework from './components/Firework.vue';
import Modal from './components/Modal.vue';

export default {
  name: 'App',
  components: {
    Emulator,
    Firework,
    Modal,
  },
  created() {
    const uri = window.location.search.substring(1);
    const params = new URLSearchParams(uri);
    const code = decodeURIComponent(params.get('m'));

    if (code !== 'null' && code) {
      try {
        let decrypted = CryptoJs.AES.decrypt(code, this.funnyWord);
        decrypted = decrypted.toString(CryptoJs.enc.Utf8);
        const {
          answer,
          messages,
        } = JSON.parse(decrypted);
        this.result = answer;
        this.text = messages.map((t) => `<p>${t}</p>`).join('');
      } catch (error) {
        alert('Invalid URI');
      }
    } else {
      const defaultStr = '&-0123456789ABCDEFGHIJKLMNOPQR/STUVWXYZ:#@\'="¢.<(+|!$*);^ ,%_>?';
      this.$store.commit('setResult', defaultStr);
    }
  },
  methods: {
    onCheckResultClick() {
      const str = this.$store.getters.stringResult.trimRight();
      this.isCorrectResult = str === this.result;
      this.resultMsg = this.isCorrectResult ? 'верный' : 'ошибка';
      if (this.isCorrectResult) {
        this.showFireworks = true;
        this.resultMsg = 'верный';

        setTimeout(() => {
          this.showFireworks = false;
        }, 3000);
      } else {
        this.resultMsg = 'ошибка. Попробуйте еще раз!';
      }
    },
    onClearClick() {
      this.$store.commit('clearResult');
      this.setEmptyResultMsg();
    },
    onValueChange(e) {
      this.$store.commit('addToResult', e);
      this.setEmptyResultMsg();
    },
    setEmptyResultMsg() {
      this.resultMsg = '&nbsp;';
    },
  },
  data: () => ({
    result: '',
    text: '',
    funnyWord: 'PxHAWuIgb0',
    isCorrectResult: null,
    resultMsg: '&nbsp;',
    showFireworks: false,
    showModal: false,
  }),
};
</script>

