import Vue from 'vue';
import Vuex from 'vuex';
import { getChars, getKeyByChar } from '../utils/chars';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    result: Array.from({ length: 72 }).fill(0),
  },
  mutations: {
    addToResult(state, { column, value }) {
      state.result[column] = value;
      Vue.set(state, 'result', [...state.result]);
    },
    setResult(state, str) {
      const result = [];
      for (let i = 0; i < str.length; i += 1) {
        const char = str[i];
        result.push(+getKeyByChar(char));
      }
      state.result = result;
    },
    clearResult(state) {
      state.result = Array.from({ length: 72 }).fill(0);
    },
  },
  getters: {
    stringResult: (state) => {
      let str = '';
      const chars = getChars();
      state.result.forEach((key) => {
        const char = chars[key];
        str += char;
      });
      return str.trimRight();
    },
  },
  actions: {
  },
  modules: {
  },
});
