<template>
  <div class="punch-card-wrapper">
    <div class="punch-card-block">
      <EmulatorColumn
        v-for="i in 80"
        :key="`column-${i}`"
        :column="i"
        :val="str[i-1]"
        @handleValueChange="onValueChange"
      />
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import EmulatorColumn from './EmulatorColumn.vue';
import { getKeyByChar } from '../utils/chars';

export default {
  props: {
    str: {
      type: Array,
    },
  },
  components: {
    EmulatorColumn,
  },
  created() {
  },
  methods: {
    getKeyByChar(str, column) {
      const val = +getKeyByChar(str[column]);
      this.$store.commit('addToResult', {
        column,
        value: val,
      });

      return val;
    },
    getKeysByStr(str) {
      const result = [];
      for (let i = 0; i < str.length; i += 1) {
        const char = str[i];
        result.push(+getKeyByChar(char));
      }
      return result;
    },
    onValueChange(e) {
      this.$emit('handleValueChange', e);
    }
  },
  computed: {
  },
  data: () => ({
  }),
};
</script>

