<template>
  <div
    :class="[
      'punch-card-column',
      (column > 72) ? 'no-select' : 'select',
      (column === 72) && 'right-column-border',
    ]"
  >
    <div
      class="punch-card-cell value"
      :style="{ color: humanValColor }"
    >
      {{ this.humanVal }}
    </div>
    <template v-for="cell of cells">
      <div
        :key="`cell-${cell.row}`"
        class="punch-card-cell"
        :class="[
          'punch-card-cell',
          (column > 72) ? 'no-select' : 'select',
          cell.isSelected && 'selected'
        ]"
        :row="cell.row"
        @click="() => onHandleClick(cell.row)"
      >
        <template v-if="cell.row - 1 > 0">
          {{ cell.row - 2}}
        </template>
      </div>
      <div
        class="punch-card-cell number"
        :key="`cell-num-${cell.row}`"
        v-if="cell.row === 1 || cell.row === cells.length - 2"
      >
        {{ column }}
      </div>
    </template>
  </div>
</template>

<script>
import { getChars } from '../utils/chars';

export default {
  props: {
    column: {
      type: Number,
      required: true,
    },
    val: {
      type: Number,
      default: 0,
    },
  },
  methods: {
    init() {
      this.chars = getChars();
      this.cells = Array.from({ length: 12 }, (_, v) => ({
        row: v,
        isSelected: false,
      }));
    },
    onHandleClick(row) {
      const {
        isSelected,
      } = this.cells[row];

      this.cells[row].isSelected = !isSelected;
      if (!isSelected) {
        this.$emit('handleValueChange', {
          value: this.val | (1 << row), // eslint-disable-line
          column: this.column - 1,
        });
      } else {
        this.$emit('handleValueChange', {
          value: this.val ^ (1 << row), // eslint-disable-line
          column: this.column - 1,
        });
      }
    },
    setSelectedCells() {
      const { val } = this;
      for (let i = 0; i < this.cells.length; i += 1) {
        const cell = this.cells[i];
        const rowVal = 1 << cell.row; // eslint-disable-line
        if (val & rowVal) { // eslint-disable-line
          this.cells[i].isSelected = true;
        } else {
          this.cells[i].isSelected = false;
        }
      }
    },
  },
  created() {
    this.init();
    this.setSelectedCells();
  },
  watch: {
    val() {
      this.setSelectedCells();
    },
  },
  computed: {
    charVal() {
      return this.chars[this.val];
    },
    humanVal() {
      return this.charVal || '�';
    },
    humanValColor() {
      return this.charVal ? 'black' : 'red';
    },
  },
  data: () => ({
    chars: [],
    cells: [],
  }),
};
</script>

<style scoped lang="scss">
.punch-card-column {
  display: flex;
  flex-direction: column;
  margin: 1px;
  border: 1px solid #f8f696;
  box-sizing: border-box;
  user-select: none;
  font-size: 14px;
  position: relative;

  &.select {
    &:hover {
      border: 1px double rgb(226, 170, 66);
      cursor: default;
    }

    .select {
      &:not(.selected):hover {
        background-color: rgba(221, 93, 18, 0.6);
      }
    }
  }

  &.right-column-border {
    &::after {
      content: "";
      width: 1px;
      height: 100%;
      background-color: black;
      position: absolute;
      right: -2px;
    }
  }

  .punch-card-cell {
    margin: 1px 0;
    width: 13px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;

    &.number {
      font-size: 9px;
      height: fit-content;
    }
    &.selected {
      background-color: rgb(34, 29, 29);
      color: rgb(34, 29, 29);

      &:hover {
        color: #ffffff;
      }
    }
  }
}
</style>
